}

input[type="checkbox"] {
    width: 50px;
    height: 25px;
    position: relative;
    -webkit-appearance: none;
    appearance: none;
    background: #ccc;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s;
    margin-top: 10%;
  }
@media only screen and (min-width : 750px) {
  input[type="checkbox"] {
    margin-top: 0;
  }
}
  input[type="checkbox"]:before {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    width: 21px;
    height: 21px;
    background: white;
    border-radius: 50%;
    transition: all 0.3s;
  }
  
  input[type="checkbox"]:checked:before {
    left: 27px;
  }
